<template>
  <div class="ao-asset-table">
    <div class="header">
      <div class="left">
        <span>{{ title }}</span>
        <span>{{ moneyUnit }}</span>
        <span>{{ ffff(totalMoney) }}</span>
      </div>
      <div class="right" @click="path('/address', { id: id, title: address_title })">{{ address_title }}</div>
    </div>
    <!-- 表格 -->
    <div class="table">
      <div class="theader">
        <div class="th">
          <div class="td">{{ td[0] }}</div>
          <div class="td">{{ td[1] }}</div>
          <div class="td">{{ td[2] }}</div>
          <div class="td">{{ td[3] }}({{ moneyUnit }})</div>
          <div class="td">{{ td[4] }}</div>
        </div>
      </div>
      <div class="tbody">
        <div class="tr" v-for="(item, index) in list" :key="index">
          <div class="td">{{ item.currency }}</div>
          <div class="td">{{ ffff(item.positions) }}</div>
          <div class="td">{{ ffff(item.positions_lock) }}</div>
          <div class="td">{{ ffff(item.zh) }}</div>
          <div class="td">
            <span class="btn" @click="path('/recharge', { address: item.address, title: btn[0], currency: item.currency, type: id, positions: item.positions,positions_lock:item.positions_lock,zh:item.zh })">{{ btn[0] }}</span>
            <span class="btn" @click="path('/money', { id: item.id, title: btn[1], money: ffff(totalMoney),currency: item.currency, unit: moneyUnit, type: id,positions: item.positions,positions_lock:item.positions_lock,zh:item.zh })">{{ btn[1] }}</span>
            <span class="btn" @click="path('/record', { id: item.id, title: btn[2], money: ffff(totalMoney),currency: item.currency, unit: moneyUnit, type: id,positions: item.positions,positions_lock:item.positions_lock,zh:item.zh })">{{ btn[2] }}</span>
            <span v-if="item.transfer ? true : false" class="btn" @click="path('/transfer',{ id: item.id, title: btn[3], money: ffff(totalMoney),currency: item.currency, unit: moneyUnit, type: id,positions: item.positions,positions_lock:item.positions_lock,zh:item.zh })">{{ btn[3] }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAsset } from '@/api/asset'

export default {
  props: {
    id: [Number, String]
  },
  data() {
    return {
      title: this.$t('asset.table_title'),
      address_title: this.$t('asset.table_address_title'),
      td: this.$t('asset.table_td'),
      btn: this.$t('asset.table_btn'),

      list: [],
      moneyUnit: '',
      totalMoney: '0.00000000',
    };
  },
  mounted() {
    getAsset({ id: this.id }).then(res => {
      this.totalMoney = res.totalMoney
      this.list = res.list
      this.moneyUnit = res.moneyUnit
      this.list2 = res.list2
      this.show = true
    })
  },
  methods: {
    ffff(v, l=4) {
      return parseFloat(v).toFixed(l)
    },
    path(p, o) {
      this.$store.dispatch('updateData', o)
      localStorage.setItem('jyxt_asset_table_path', JSON.stringify(o));
      this.$router.push({ path: p })
    }
  }
};
</script>

<style scoped lang="scss">
.ao-asset-table {
  .header {
    // display: flex;
    // align-items: center;
    // justify-content: space-between;
    padding: 15px 30px;
    overflow: hidden;
    color: #c7cce6;
    .left {
      float: left;
    }
    .right {
      float: right;
      color: #5697f4;
      cursor: pointer;
    }
    span {
      color: #c7cce6;
    }
  }
  .theader,.tbody{
    padding: 0 20px;
  }
  .th,.tr {
    display: flex;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 12px;
    flex: 1;
    text-align: center;
  }
  .tr {
    padding: 15px 0;
    border-bottom: 1px solid #1e2c42;
  }
  .tr .td {
    color: #c7cce6;
    .btn{
      color: #5697f4;
      font-size: 12px;
      cursor: pointer;
      &+.btn{
        padding-left:5px;
      }
    }
  }
}
</style>



